import React from 'react';
import { NavLink } from 'react-router-dom';

export default function Navbar() {
    return (
        <>
     <div className="container-fluid">
       <div className="row">
         <div className="col-10 mx-auto">
         <nav className="navbar navbar-expand-lg navbar-light bg-white">
  <div className="container-fluid">
    <NavLink exact className="navbar-brand" id="navbar-brand" to="/">ReactApp</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mx-auto mb-2 mb-lg-2">
      <li className="nav-item">
          <NavLink exact activeClassName="menu-active" className="nav-link active " aria-current="page" to="/">Home</NavLink>
        </li> 
        <li className="nav-item">
          <NavLink activeClassName="menu-active" className="nav-link active " aria-current="page" to="/service">Service</NavLink>
        </li> 
        <li className="nav-item">
          <NavLink   activeClassName="menu-active"className="nav-link active " aria-current="page" to="/contact">Contact</NavLink>
        </li>
         <li className="nav-item">
          <NavLink  activeClassName="menu-active"className="nav-link active " aria-current="page" to="/about">About</NavLink>
        </li> 
      </ul>
    </div>
  </div>
</nav>
         </div>
       </div>
     </div>
      
        </>
    )
}
